<script>
  import axios from "axios";

  export default {
      name: 'dashboard',
      data() {
            return {
              name: localStorage.getItem('ms_username'),
              nowDate: "none",
              City:"未获取定位",
              img:'',
              infoList:{
                name:'zmns',
                qq:'402887431',
                email:'402887431@qq.com',
                img:'../../assets/img/img.jpg',
                slogan:''
              },
              nameVisable:true,
              qqVisable:true,
              emailVisable:true,
              sloganVisable:true,
              infodata:{
                users:0,
                articles:1,
                comments:0
              },
            }
        },
    mounted() {
      this.currentTime();
      this.City=localStorage.getItem('City');
      axios({
        url:'/admin/getINFO',
        method:'GET',
      }).then(res =>{
        this.infoList.name = res.data.name;
        this.infoList.qq = res.data.qq;
        this.infoList.email = res.data.email;
        this.infoList.img = res.data.img;
        this.img = res.data.img;
        // this.getElementById("img").src=res.data.img;
        this.infoList.slogan = res.data.slogan;
      });
      axios({
        url:'/admin/getDashboard',
        method:'GET'
      }).then(res =>{
        this.infodata.users= res.data.users;
        this.infodata.articles = res.data.articles;
        this.infodata.comments = res.data.comments;
      })
    },
        components: {
        },
        computed: {

        },
        methods: {
        imgsrc(res,file){
          this.infoList.img = res;
          this.img = URL.createObjectURL(file.raw);
          console.log(this.infoList.img);
        },
          currentTime() {
            setInterval(this.formatDate, 500);
          },
          formatDate() {
            let date = new Date();
            let year = date.getFullYear(); // 年
            let month = date.getMonth() + 1; // 月
            let day = date.getDate(); // 日
            let week = date.getDay(); // 星期
            let weekArr = [ "星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六" ];
            let hour = date.getHours(); // 时
            hour = hour < 10 ? "0" + hour : hour; // 如果只有一位，则前面补零
            let minute = date.getMinutes(); // 分
            minute = minute < 10 ? "0" + minute : minute; // 如果只有一位，则前面补零
            let second = date.getSeconds(); // 秒
            second = second < 10 ? "0" + second : second; // 如果只有一位，则前面补零
            this.nowDate = `${year}-${month}-${day} ${weekArr[week]} ${hour}:${minute}:${second}`;
          },
          submitInformation(infoList){
            axios({
              url:"/admin/updateINFO",
              method: 'POST',
              data :{
                "name":infoList.name,
                "qq" :infoList.qq,
                "email": infoList.email,
                "img":infoList.img,
                "slogan":infoList.slogan
              }
            }).then(res =>{
              if(res.data.code==200){
                this.$message("更新成功")
                this.nameVisable=true;
                this.qqVisable=true;
                this.emailVisable=true;
                this.sloganVisable=true;
              }else {
                this.$message("更新失败")
              }
            })
          }
      }
}
</script>

